import React from 'react';
import { Highlighter } from '../../utils/highlighter';

import ListItem from '../../../base/listItem';
import PageTitle from '../../../base/pageTitle';
import WordsList from '../../../base/keywordsList';
import eventData from './data.json';

export default function TechEvent(props) {
  const data = eventData;
  const linkListItems = data.linkListItems.slice();
  const reactQues = data.reactQues.slice();
  return (
    <div className="tech tech-event">
      <header>
        <PageTitle title={data.pageTitle} />
        <WordsList words={data.keyWords} />
      </header>
      <article>
        <div className="object">
          <h2>Object</h2>
          {reactQues.map((item, index) => (
            <ListItem items={item.items} title={item.title} key={index} />
          ))}
          <dl>
            <dt>,</dt>
            <dt>instanceof</dt>
            <dd>
              <Highlighter>
                {`
// returns false, checks the prototype chain, finds undefined
'simpleStr' instanceof String; 
`}
              </Highlighter>
            </dd>

            <dt>__proto__</dt>
            <dt>__proto__prototype</dt>
            <dt>: Function.prototypeFunction.__proto__</dt>

            <dt>constructor</dt>
            <dd>
              Each object in JavaScript has an implicit property named
              constructor that references the constructor that was used to
              create the object. And because the prototype is a property of the
              constructor, each object has a way to find its prototype by
              <b> obj.constructor.prototype</b>
            </dd>
            <dd>
              <Highlighter>
                {`function Ninja(){
    this.swung = true;
    //,,
    this.swingSword = function(){
      return !this.swung;
    };
}
//new ,
let ninja = new Ninja();
Ninja.prototype.swingSword = function(){
  return this.swung;
};
//return false
ninja.swingSword()
//,
let ninja2 = new ninja.constructor();
//true
console.log(ninja2 instanceof Ninja);
//false 
console.log(ninja2 === ninja);
`}
              </Highlighter>
            </dd>
            <dt></dt>
            <dd>
              <Highlighter>
                {`
function Person(){}
Person.prototype.dance = function(){};

function Ninja(){};
Ninja.prototype = new Person();
var ninja = new Ninja();
/*
: Ninja.prototype = Person.prototype,
: ,NinjaPerson
*/
// instanceof (Person/Ninja.prototype)(ninja),
// (),
assert(ninja instanceof Ninja,"ninja receives functionality from the Ninja prototype");
assert(ninja instanceof Person, "... and the Person prototype");
assert(ninja instanceof Object, "... and the Object prototype");
assert(typeof ninja.dance == "function", "... and can dance!")
`}
              </Highlighter>
            </dd>
          </dl>

          <h2></h2>
          <dl>
            <dd className="refer">
              
              sJavaScriptnew
              String(s)
              6.2.2
              
              Number()Boolean()
              nullundefined
            </dd>
            <dd className="refer">
              
              
              
              
            </dd>
            <dd>
              <Highlighter>
                {`
var s="hello world!";//
//,JavaScriptnew String(s)
var word=s.substring(s.indexOf("")+1,s.length);
s.len=4
//tundefined,(s.len)
var t=s.len

var s="test",n=1,b=true;//
var S=new String(s);//
var N=new Number(n);//
var B=new Boolean(b);//
console.log('S==s:',S==s) 
console.log('S===s:',S===s)
console.log('typeof s:',typeof s)
console.log('typeof S:',typeof S)
console.log('N==n:',N==n)
console.log('N===n:',N===n)
console.log('B==b:',B==b)
console.log('B===b:',B===b)
              `}
              </Highlighter>
            </dd>
          </dl>

          <h2></h2>
          <dl>
            <dd className="refer">
              JavaScript
              method
            </dd>
            <dd>
              <Highlighter>
                {`
//aasort()asort()
a.sort();//sort(a)
              `}
              </Highlighter>
            </dd>
          </dl>

          <dd>
            <Highlighter>
              {`
              
              `}
            </Highlighter>
          </dd>
        </div>
        {linkListItems.map((item, index) => (
          <ListItem items={item.items} title={item.title} key={index} />
        ))}
      </article>
    </div>
  );
}



